//imports
@import "../../index.scss";

.wrapper {
  // border: 1px solid;
  width: 95%;

  & > h2 {
    color: $primaryColor;
    text-align: center;
    font-size: 4rem;
    font-weight: 700;
    text-transform: uppercase;
  }

  & > span {
    position: fixed;
    right: 2rem;
    bottom: 2rem;
  }
}

.addBtn {
  background-color: $primaryColor !important;
  color: $white !important;

  &:hover {
    color: $primaryColor !important ;
    background-color: $white !important;
  }
}

.createCategory {
  display: flex;
  align-items: center;
  justify-content: center;
}

.categoryList {
  font-size: 2rem;
  width: 100%;
  & table {
    text-align: center;
    & th,
    & td {
      vertical-align: middle;
    }
  }
  & thead th {
    font-size: 2rem;
  }

  & tbody {
    & tr:nth-child(even) {
      background-color: rgb(222, 240, 248);
    }
  }
}

.categoryAction {
  cursor: pointer;
}

.image {
  width: 15rem;
  object-fit: cover;
  object-position: center;
}

.toUpload {
  background-color: $primaryLight;
}


.productsWrapper{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.pagination{
  @include totalCenter();
  margin: 4rem 0;   
}

@media only screen and (max-width : 800px){
  .productsWrapper{
    justify-content: center;
    &>div{
      margin:2rem;
    }
  }
}
